<template>
    <div class="novel-link">
      <div v-if="$route.name === '小说链接'">
        <Button type="primary" size="large" to="/manage/links/novel/create" class="create" style="margin-bottom: 30px;">新增小说链接</Button>
        <Table class="novel-table" :columns="novelHeader" :data="novelData"></Table>
      </div>
      <router-view></router-view>
    </div>
</template>

<script>
import Clipboard from "clipboard";
new Clipboard(".copyBtn");
export default {
  data() {
    return {
      novelData: [],
      domain_names: [],
      novelHeader: [
        {
          key: "id",
          title: "id",
          align: "center"
        },
        {
          title: "类型",
          align: "center",
          render: (h, params) => {
            let type =
              params.row.type == 0
                ? "微跳"
                : params.row.type == 1
                  ? "非微调单页"
                  : "非微调多页";
            return h(
              "span",
              {
                props: {}
              },
              type
            );
          }
        },
        {
          key: "name",
          title: "公号",
          align: "center"
        },
        {
          title: "图片",
          align: "center",
          render: (h, params) => {
            let src = this.domain_names[0].domain_name + params.row.picurl;
            return h("img", {
              attrs: {
                src: src,
                style: "width: 100%;padding-top: 5px;"
              }
            });
          }
        },
        {
          key: "channel",
          title: "渠道",
          align: "center"
        },
        {
          key: "remarks",
          title: "备注",
          align: "center"
        },
        {
          title: "复制链接",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h(
              "Button",
              {
                class: "copyBtn",
                attrs: {
                  "data-clipboard-text":
                    that.domain_names[0].domain_name +
                    "/tuiguang/" +
                    (params.row.type == 0
                      ? "weitiao/"
                      : params.row.type == 1
                        ? "singlepage/"
                        : "multipage/") +
                    params.row.id
                },
                props: {
                  type: "success",
                  size: "small"
                },
                on: {
                  click() {
                    that.copy();
                  }
                }
              },
              "复制链接"
            );
          }
        },
        {
          title: "Action",
          align: "center",
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改小说链接",
                        params: {
                          row: params.row,
                          index: params.index,
                          domain_name: that.domain_names[0].domain_name
                        }
                      });
                    }
                  }
                },
                "修改"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.deleteOne(params.row, params.index);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.show();
  },
  methods: {
    deleteOne(row, index) {
      let that = this;
      this.$Modal.confirm({
        title: "提示:",
        content: "确认删除这条内容吗？",
        onOk: () => {
          that.$axios
            .post("/tuiguang/novel/delete_one", { id: row.id })
            .then(res => {
              that.$Message.info(res.data.message);
              that.novelData.splice(index, 1);
            });
        }
      });
    },
    copy() {
      this.$Message.info("复制成功");
    },
    show() {
      this.$axios.get("/tuiguang/novel/show").then(res => {
        this.novelData = res.data.data;
        this.domain_names = res.data.domain_names;
      });
    }
  },
  watch: {
    $route(to, from) {
      if (this.$route.params.state != "cancel") {
        if (from.name == "创建小说链接") {
          this.novelData.push(this.$route.params);
        } else if (from.name == "修改小说链接") {
          this.novelData.splice(
            this.$route.params.index,
            1,
            this.$route.params.row
          );
        }
      }
    }
  }
};
</script>
